import React from "react";
import './Facerecoginition.css';

const Facerecoginition = ({imageUrl , box}) => {
    return (
        <div className="center ma">
            <div className="absolute mt2">
                <img src={imageUrl} alt="my-face" width='500' height='auto' id="inputimage" />
                <div className="bounding-box" style={{top : box.topRow , bottom : box.bottomRow , right : box.rightCol , left : box.leftCol}}>

                </div>
            </div>
        </div>
    )
}

export default Facerecoginition;