import React from "react";

const Profile = ({name , email , joined}) => {
    return (
        <article className="br3 ba shadow-5 b--black-10 mv5 w-100 w-50-m w-25-l mw6 center">
                <main className="pa4 black-80">
                    <div className="measure">
                        <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">name</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" value={name} type="email" name="email-address"  id="email-address" disabled />
                        </div>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" value={email} type="email" name="email-address"  id="email-address" disabled />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6" htmlFor="password">joined att</label>
                            <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" value={joined} type="text" name="password"  id="password" disabled />
                        </div>
                        </fieldset>
                       
                    </div>
                </main>
            </article>
    )
}

export default Profile;