import React from "react";

const Navigation = ({onRouteChange , isSignedIn , route}) => {
    
        if(isSignedIn )
        {
            return (
                <nav style={{display : 'flex'  , justifyContent : 'flex-end'}}>
                    {
                        route === "profile"
                        ?
                        <p onClick={() => onRouteChange('home')} className="fe dim black udnerline pa3 pointer">home</p>
                        :
                        <div style={{display : "flex" , flexDirection : "row"}}> 
                             <p onClick={() => onRouteChange('profile')} className="fe dim black underline pa3 pointer">Profile</p>
                             <p onClick={ () => onRouteChange('signout')} className="fe dim black underline pa3 pointer">sign out</p>
                        </div>
                       
                    }
                  
                </nav>
            )
            
        } else {
            return (
                <nav style={{display : 'flex'  , justifyContent : 'flex-end'}}>
                    <p onClick={ () => onRouteChange('signin')} className="fe dim black underline pa3 pointer">signin</p>
                    <p onClick={ () => onRouteChange('register')} className="fe dim black underline pa3 pointer"> register</p>
                </nav>
            )
           
        }
        
    
}

export default Navigation;