import React from 'react';
import './App.css';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageLinkFrom from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank';
import Facerecoginition from './components/Facerecoginition/Facerecoginition';
import 'tachyons';
import Register from './components/Register/Register';
import SignIn from './components/SignIn/SignIn';
import CustomParticle from './components/Particle/CustomParticle';
import Profile from './components/Profile/Profile';
import Clarifai from 'clarifai';
window.process = {};

const app = new Clarifai.App({
  apiKey: "60693b9d75b84b639a530b3a432ec030"
});


class App extends React.Component {
  constructor() {
    super();
    this.state = {
      input : '',
      imageUrl : '',
      box : {} ,
      route : 'signin' , 
      isSignedIn : false ,
      user : 
      {
          id : '' ,
          name : '', 
          email : '' , 
          entries : 0 ,
          joined : new Date()
      }
    }
  }

  loadUser = (data) => {
    this.setState({user : {
      id : data.id , 
      name : data.name,
      email : data.email,
      entries : data.entries ,
      joined : data.joined
    }})
  }
  

  calculateFaceLocation = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputimage');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      leftCol : clarifaiFace.left_col * width,
      topRow : clarifaiFace.top_row * height,
      rightCol : width - (clarifaiFace.right_col * width) ,
      bottomRow : height - (clarifaiFace.bottom_row * height)
    }
  }

  displayFaceBox = (box) => {
    console.log(box);
    this.setState({box : box});
  }

  onInputChange = (event) => {
    this.setState({input : event.target.value});
  }

  onButtonSubmit = () => {
    this.setState({imageUrl : this.state.input});
    app.models
    .predict(
      {
        id : "a403429f2ddf4b49b307e318f00e528b",
        version : "34ce21a40cc24b6b96ffee54aabff139"
      },
        this.state.input
    )
    .then(response =>{
      if(response) {
        fetch('http://localhost:3001/image' , {
            method : "put" ,
            headers : {'Content-type' : 'application/json'} ,
            body : JSON.stringify({
              id : this.state.user.id , 
            })  
          })
          .then(response => response.json())
          .then(count => {
            this.setState(Object.assign(this.state.user.entries = count))

          })
         
       
      
      this.displayFaceBox(this.calculateFaceLocation(response))
     } 
       })
    
    .catch(err => console.log(err));

  }

  onRouteChange = (route) => {
      if(route === 'signout') {
        
        this.setState({isSignedIn : false})
      } else if (route === 'home') {

        this.setState({isSignedIn : true})
      }
    this.setState({route : route});
  }
  render() {
    const  { isSignedIn , imageUrl , route , box } = this.state;
    var output;
    if(route === 'home') {
      output =
      <div>
        <Logo />
        <Rank name ={this.state.user.name} entries = {this.state.user.entries} />
        <ImageLinkFrom 
            onInputChange = {this.onInputChange}
            onButtonSubmit = {this.onButtonSubmit} />
        <Facerecoginition box = {box} imageUrl = {imageUrl} />
      </div>
    } else if(route === "profile") {
      output = <Profile name ={this.state.user.name} joined = {this.state.user.joined} email = {this.state.user.email} />

    } else if(route === "signin") {
      output = <SignIn onRouteChange = {this.onRouteChange} loadUser={this.loadUser} />
    } else {
      output = <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
    } 
    return (
      <div>
        <CustomParticle className="particles" />
        <Navigation onRouteChange={this.onRouteChange} isSignedIn={isSignedIn} route={this.state.route} />
        {
          output
        }
      </div>
    )
  }; 
    
  
}

export default App;

